/* components/Projects.css */
.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 20px;
  padding: 20px;
  justify-content: center;
  align-items: center;

}

.projects-wrapper {
  margin-top: -400px;
  margin-bottom: 50px;
}

.project-card {
  background-color: #1E1E1E;
  padding: 20px;
  border-radius: 4px;
  text-align: center;
}

.project-title {
  font-size: 1.2rem;
  font-weight: bold;
}

.project-description {
  font-size: 0.9rem;
  margin-top: 10px;
}

.project-link {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 10px;
  background-color: #4A4A4A;
  color: #FFFFFF;
  text-decoration: none;
  border-radius: 4px;
}

.project-link:hover {
  background-color: #3A3A3A;
}
