/* components/Footer.css */
.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1E1E1E;
  color: #FFFFFF;
  padding: 20px;
  font-size: 0.9rem;
}

.contact-info,
.social-links {
  margin-bottom: 10px;
}

.social-links a {
  color: #FFFFFF;
  text-decoration: none;
  margin: 0 5px;
}

.social-links a:hover {
  text-decoration: underline;
}
