/* components/Navbar.css */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1E1E1E;
  padding: 10px 20px;
  font-size: 1rem;
  color: #FFFFFF;
}

.navbar-brand {
  font-weight: bold;
}

.navbar-links {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-links li {
  margin-left: 20px;
}

.navbar-links a {
  color: #FFFFFF;
  text-decoration: none;
}

.navbar-links a:hover {
  text-decoration: underline;
}
